import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTable } from "@fortawesome/free-solid-svg-icons";

const RangeButton = ({ gameState, setShowAnswerModal }) => {
  const latestHistory = gameState.histories[gameState.histories.length - 1];

  return (
    <>
      {latestHistory.answer && (
        <button
          id="answer-range-button"
          onClick={() => setShowAnswerModal(true)}
        >
          <FontAwesomeIcon icon={faTable} size="2x" fixedWidth inverse />
        </button>
      )}
    </>
  );
};

export default RangeButton;
