import ReactModal from "react-modal";
import RangeTable from "./ranges/RangeTable";
import openRange from "./ranges/open";
import facingRaiseRange from "./ranges/facingRaise";
import { rangeTableName } from "./ranges/range";

import "./style/RangeModal.css";

const RangeModal = ({ showRangeModal, setShowRangeModal }) => {
  const style = {
    content: {
      background: "rgba(255, 255, 255, 0.8)",
      borderRadius: "10px",
      width: "50%",
      left: "0",
      right: "0",
      margin: "auto",
    },
  };

  return (
    <ReactModal isOpen={showRangeModal} style={style} ariaHideApp={false}>
      <div id="close-button">
        <button
          onClick={() => {
            setShowRangeModal(false);
          }}
        >
          <span className="batsu"></span>
        </button>
      </div>
      <div className="ranges">
        <h3>Open Range</h3>
        {openRange.map((range, idx) => (
          <div className="range-block" key={idx}>
            <div>{rangeTableName(idx, null)}</div>
            <RangeTable range={range} />
          </div>
        ))}
        <h3>Facing Raise Range</h3>
        {facingRaiseRange.map((ranges, i) => {
          return ranges.map((range, j) => {
            if (range.length) {
              return (
                <div
                  className="range-block"
                  key={i * facingRaiseRange.length + j}
                >
                  <div>{rangeTableName(j, i)}</div>
                  <RangeTable range={range} />
                </div>
              );
            } else {
              return <></>;
            }
          });
        })}
      </div>
    </ReactModal>
  );
};

export default RangeModal;
