import React from "react";
import {
  TwitterShareButton,
  TwitterIcon,
  TelegramShareButton,
  TelegramIcon,
  WhatsappShareButton,
  WhatsappIcon,
} from "react-share";

const emojiCharacters = require("./emojiCharacters");

const ShareButtons = () => {
  const share_title = [...Array(10)]
    .map(() => [...Array(10)].map(() => emojiCharacters["Joker"]).join(""))
    .join("\n");
  const share_link = "https://flople.net";
  return (
    <div className="sharing-buttons">
      <WhatsappShareButton url={share_link} title={share_title}>
        <WhatsappIcon size={50} round />
      </WhatsappShareButton>
      <TwitterShareButton
        url={share_link}
        title={share_title + "\n"}
        hashtags={["poker"]}
        via={"flople"}
      >
        <TwitterIcon size={50} round />
      </TwitterShareButton>
      <TelegramShareButton url={share_link} title={share_title}>
        <TelegramIcon size={50} round />
      </TelegramShareButton>
    </div>
  );
};

export default ShareButtons;
