import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircle } from "@fortawesome/free-regular-svg-icons";
import { faXmark } from "@fortawesome/free-solid-svg-icons";

import { raiseColor, callColor, foldColor } from "./Utils";
import AnswerModal from "./AnswerModal";

import "./style/Answer.css";

const Answer = ({ gameState, showAnswerModal, setShowAnswerModal }) => {
  const latestHistory = gameState.histories[gameState.histories.length - 1],
    isCorrect = latestHistory.isCorrect,
    answer = latestHistory.answer;

  if (answer) {
    const style = {
      backgroundImage: `linear-gradient(to right, ${raiseColor} ${
        answer[0]
      }%, ${callColor} ${answer[0]}% ${answer[0] + answer[1]}%, ${foldColor} ${
        answer[0] + answer[1]
      }% 100%`,
    };

    return (
      <>
        <div id="data" style={style}>
          <div id="judge">
            {isCorrect ? (
              <>
                <FontAwesomeIcon className="icon-circle" icon={faCircle} />
                <FontAwesomeIcon className="icon-circle-2" icon={faCircle} />
                <FontAwesomeIcon className="icon-circle-3" icon={faCircle} />
              </>
            ) : (
              <FontAwesomeIcon className="icon-xmark" icon={faXmark} />
            )}
          </div>
          <div id="result-raise">raise: {answer[0]}%</div>
          <div id="result-call">call: {answer[1]}%</div>
          <div id="result-fold">fold: {answer[2]} %</div>
        </div>
        <AnswerModal
          showAnswerModal={showAnswerModal}
          setShowAnswerModal={setShowAnswerModal}
          gameState={gameState}
        />
      </>
    );
  } else {
    return (
      <div id="data">
        <div id="result-no-answer">Currently no solution</div>
      </div>
    );
  }
};

export default Answer;
