import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faForwardStep } from "@fortawesome/free-solid-svg-icons";
import "./style/NextButton.css";

const NextButton = ({ gameState, setGameState }) => {
  const updatePlayerIdx = () => {
    let nextPlayerIdx = (gameState.currentPlayerIdx + 1) % gameState.numPlayers;
    let cnt = 0;
    while (
      gameState.actions[nextPlayerIdx] === "fold" &&
      cnt < gameState.numPlayers
    ) {
      nextPlayerIdx = (nextPlayerIdx + 1) % gameState.numPlayers;
      cnt++;
    }
    setGameState((prevState) => ({
      ...prevState,
      currentPlayerIdx: nextPlayerIdx,
    }));
  };

  return (
    <button className="next-button" onClick={() => updatePlayerIdx()}>
      <FontAwesomeIcon icon={faForwardStep} />
    </button>
  );
};

export default NextButton;
