import "./style/App.css";
import React, { useState } from "react";
import Game from "./Game";
import BurgerMenu from "./BurgerMenu";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTable } from "@fortawesome/free-solid-svg-icons";
import { faCircleQuestion } from "@fortawesome/free-regular-svg-icons";
import UsageModal from "./UsageModal";
import RangeModal from "./RangeModal";

const App = () => {
  const [showUsageModal, setShowUsageModal] = useState(false);
  const [showRangeModal, setShowRangeModal] = useState(false);

  const suits = ["", "", "", ""];

  return (
    <div id="App">
      <BurgerMenu />
      <header>
        <div className="menu-left">
          <button
            id="help-button"
            aria-label="help-button"
            className="icon"
            onClick={() => {
              setShowUsageModal(true);
            }}
          >
            <game-icon icon="help">
              <FontAwesomeIcon
                icon={faCircleQuestion}
                size="2x"
                fixedWidth
                inverse
              />
            </game-icon>
          </button>
        </div>
        <div className="title">
          <span className="character-f">F</span>
          <span className="character-l">l</span>
          <div className="empty-text">
            <span className="suit">
              {suits[Math.floor(Math.random() * suits.length)]}
            </span>
            <span className="character-o">o</span>
          </div>
          <span>p</span>
          <span>l</span>
          <span>e</span>
        </div>
        <div className="menu-right">
          <button
            id="range-button"
            aria-label="range-button"
            onClick={() => {
              setShowRangeModal(true);
            }}
          >
            <FontAwesomeIcon icon={faTable} size="2x" fixedWidth inverse />
          </button>
        </div>
      </header>
      <UsageModal
        showUsageModal={showUsageModal}
        setShowUsageModal={setShowUsageModal}
      />
      <RangeModal
        showRangeModal={showRangeModal}
        setShowRangeModal={setShowRangeModal}
      />
      <Game />
    </div>
  );
};

export default App;
