import React from "react";

import I2c from "./images/2C.svg";
import I3c from "./images/3C.svg";
import I4c from "./images/4C.svg";
import I5c from "./images/5C.svg";
import I6c from "./images/6C.svg";
import I7c from "./images/7C.svg";
import I8c from "./images/8C.svg";
import I9c from "./images/9C.svg";
import ITc from "./images/TC.svg";
import IJc from "./images/JC.svg";
import IQc from "./images/QC.svg";
import IKc from "./images/KC.svg";
import IAc from "./images/AC.svg";

import I2d from "./images/2D.svg";
import I3d from "./images/3D.svg";
import I4d from "./images/4D.svg";
import I5d from "./images/5D.svg";
import I6d from "./images/6D.svg";
import I7d from "./images/7D.svg";
import I8d from "./images/8D.svg";
import I9d from "./images/9D.svg";
import ITd from "./images/TD.svg";
import IJd from "./images/JD.svg";
import IQd from "./images/QD.svg";
import IKd from "./images/KD.svg";
import IAd from "./images/AD.svg";

import I2h from "./images/2H.svg";
import I3h from "./images/3H.svg";
import I4h from "./images/4H.svg";
import I5h from "./images/5H.svg";
import I6h from "./images/6H.svg";
import I7h from "./images/7H.svg";
import I8h from "./images/8H.svg";
import I9h from "./images/9H.svg";
import ITh from "./images/TH.svg";
import IJh from "./images/JH.svg";
import IQh from "./images/QH.svg";
import IKh from "./images/KH.svg";
import IAh from "./images/AH.svg";

import I2s from "./images/2S.svg";
import I3s from "./images/3S.svg";
import I4s from "./images/4S.svg";
import I5s from "./images/5S.svg";
import I6s from "./images/6S.svg";
import I7s from "./images/7S.svg";
import I8s from "./images/8S.svg";
import I9s from "./images/9S.svg";
import ITs from "./images/TS.svg";
import IJs from "./images/JS.svg";
import IQs from "./images/QS.svg";
import IKs from "./images/KS.svg";
import IAs from "./images/AS.svg";

import B from "./images/back.svg";

const cards = {
  "2c": I2c,
  "3c": I3c,
  "4c": I4c,
  "5c": I5c,
  "6c": I6c,
  "7c": I7c,
  "8c": I8c,
  "9c": I9c,
  Tc: ITc,
  Jc: IJc,
  Qc: IQc,
  Kc: IKc,
  Ac: IAc,

  "2d": I2d,
  "3d": I3d,
  "4d": I4d,
  "5d": I5d,
  "6d": I6d,
  "7d": I7d,
  "8d": I8d,
  "9d": I9d,
  Td: ITd,
  Jd: IJd,
  Qd: IQd,
  Kd: IKd,
  Ad: IAd,

  "2h": I2h,
  "3h": I3h,
  "4h": I4h,
  "5h": I5h,
  "6h": I6h,
  "7h": I7h,
  "8h": I8h,
  "9h": I9h,
  Th: ITh,
  Jh: IJh,
  Qh: IQh,
  Kh: IKh,
  Ah: IAh,

  "2s": I2s,
  "3s": I3s,
  "4s": I4s,
  "5s": I5s,
  "6s": I6s,
  "7s": I7s,
  "8s": I8s,
  "9s": I9s,
  Ts: ITs,
  Js: IJs,
  Qs: IQs,
  Ks: IKs,
  As: IAs,
};

const Card = ({
  card,
  front = true,
  back,
  height,
  style = {},
  className = "",
}) => {
  style.height = height;

  if (back) {
    return (
      <img
        src={B}
        className={`react-playing-card react-playing-card__back ${className}`}
        alt="card-back"
        style={style}
      />
    );
  }

  if (front) {
    return (
      <img
        src={cards[card]}
        className={`react-playing-card react-playing-card__back ${className}`}
        alt={card}
        style={style}
      />
    );
  }
};

export default Card;
