import ReactModal from "react-modal";

import { rangeData, rangeTableName } from "./ranges/range";
import RangeTable from "./ranges/RangeTable";

const AnswerModal = ({ showAnswerModal, setShowAnswerModal, gameState }) => {
  const answerRangeStyle = {
    content: {
      background: "rgba(255, 255, 255, 0.8)",
      borderRadius: "10px",
      width: "50%",
      left: "0",
      right: "0",
      margin: "auto",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
  };

  const focusCellIdx = () => {
    const playerIdx = gameState.currentPlayerIdx,
      card1Idx = gameState.cardIndexes[playerIdx],
      card2Idx = gameState.cardIndexes[playerIdx + gameState.numPlayers],
      suited = Math.floor(card1Idx / 13) === Math.floor(card2Idx / 13);

    let firstNum = card1Idx % 13,
      secondNum = card2Idx % 13;

    if (firstNum !== 1 && firstNum < secondNum) {
      [firstNum, secondNum] = [secondNum, firstNum];
    }

    if (suited) {
      return [12 - firstNum, 12 - secondNum];
    } else {
      return [12 - secondNum, 12 - firstNum];
    }
  };

  return (
    <ReactModal
      isOpen={showAnswerModal}
      style={answerRangeStyle}
      ariaHideApp={false}
    >
      <div id="close-button">
        <button onClick={() => setShowAnswerModal(false)}>
          <span className="batsu"></span>
        </button>
      </div>
      <div className="range-block">
        <div>
          {rangeTableName(gameState.currentPlayerIdx, gameState.openRaiserIdx)}
        </div>
        <RangeTable
          range={rangeData(gameState)}
          focusCellIdx={focusCellIdx()}
        />
      </div>
    </ReactModal>
  );
};

export default AnswerModal;
