import React from "react";
import { rangeAction } from "./ranges/range";
import "./style/ActionButton.css";
import Tooltip from '@mui/material/Tooltip';

const ActionButton = ({ gameState, setGameState }) => {
  const playerIdx = gameState.currentPlayerIdx;

  const correctAction = (answer) => {
    if (!answer) return null;
    else if (
      (gameState.actions[playerIdx] === "raise" && answer[0] !== 0) ||
      (gameState.actions[playerIdx] === "call" && answer[1] !== 0) ||
      (gameState.actions[playerIdx] === "fold" && answer[2] !== 0)
    ) {
      return true;
    } else {
      return false;
    }
  };

  const execAction = (type) => {
    const previousPoint = gameState.points[playerIdx];
    const previousAction = gameState.actions[playerIdx];

    if (type === "raise") {
      gameState.activePoint *= 3;
      gameState.points[playerIdx] = gameState.activePoint;
      gameState.numRaise += 1;
      if (gameState.openRaiserIdx === null) {
        gameState.openRaiserIdx = playerIdx;
      }
    } else if (type === "call") {
      gameState.points[playerIdx] = gameState.activePoint;
    } else if (type === "allin") {
      gameState.activePoint = 100;
      gameState.points[playerIdx] = 100;
      gameState.numRaise += 1;
    }
    gameState.actions[playerIdx] = type;

    const answer = rangeAction(gameState);

    gameState.histories.push({
      playerIdx: playerIdx,
      actionType: type,
      previousPoint: previousPoint,
      previousAction: previousAction,
      point: gameState.points[playerIdx],
      card1Idx: gameState.cardIndexes[playerIdx],
      card2Idx: gameState.cardIndexes[playerIdx + gameState.numPlayers],
      isCorrect: correctAction(answer),
      answer: answer,
    });
    setGameState({ ...gameState });
  };

  return (
    <>
      {gameState.numRaise === 3 ? (
        <Tooltip title={`All in 100BB`}>
          <button className="raise-button" onClick={() => execAction("allin")}>
            All in
          </button>
        </Tooltip>
      ) : (
        gameState.numRaise !== 4 && (
          <Tooltip title={`Raise ${gameState.activePoint * 3}BB`}>
            <button className="raise-button" onClick={() => execAction("raise")}>
              RAISE
            </button>
          </Tooltip>
        )
      )}
      <button className="call-button" onClick={() => execAction("call")}>
        CALL
      </button>
      <button className="fold-button" onClick={() => execAction("fold")}>
        FOLD
      </button>
    </>
  );
};

export default ActionButton;
