import "./style/History.css";
import { positions } from "./Utils";
import { raiseColor } from "./Utils";

const History = ({ gameState }) => {
  const histryColor = (action) => {
    if (action === "raise" || action === "allin") {
      return {
        color: raiseColor,
      };
    }
  };

  const historyRecord = (history) => {
    let record = `${positions[history.playerIdx]} ${history.actionType}`;
    if (history.actionType === "raise" || history.actionType === "allin") {
      record += ` ${history.point}bb`;
    }

    return record;
  };

  return (
    <div id="historyList">
      <ol reversed="reversed">
        {gameState.histories.map((_, idx, histories) => {
          const history = histories[histories.length - 1 - idx];
          return (
            <li key={idx} style={histryColor(history.actionType)}>
              {historyRecord(history)}
            </li>
          );
        })}
      </ol>
    </div>
  );
};

export default History;
