import "./style/Player.css";
import Hand from "./Hand";
import React from "react";
import { positions } from "./Utils";

const Player = ({ gameState }) => {
  const playerIdx = gameState.currentPlayerIdx;

  return (
    <>
      <div id="player-button-container">
        <div className="empty-box"></div>
        <div className="position">{positions[playerIdx]}</div>
        <div className="empty-box"></div>
      </div>
      <div id="card-container">
        {[...Array(gameState.numPlayers)].map((_, idx) => {
          return (
            <Hand
              firstIdx={gameState.cardIndexes[idx]}
              secondIdx={gameState.cardIndexes[idx + gameState.numPlayers]}
              visible={idx === playerIdx}
              key={idx}
            />
          );
        })}
      </div>
    </>
  );
};

export default Player;
