import React from "react";
import "./style/Accuracy.css";

const Accuracy = ({ gameState, isFinished, gameHistory }) => {
  let numCorrects = 0;
  let numActions = 0;

  const sumPoints = () => {
    let sum = 0;
    for (const point of gameState.points) {
      sum += point;
    }
    return sum;
  };

  const accurateRate = () => {
    for (const histories of gameHistory) {
      for (const history of histories) {
        if (history.answer) {
          numActions += 1;
          numCorrects += history.isCorrect;
        }
      }
    }
    if (!isFinished) {
      for (const history of gameState.histories) {
        if (history.answer) {
          numActions += 1;
          numCorrects += history.isCorrect;
        }
      }
    }
    const rate =
      numActions !== 0 ? Math.round((numCorrects / numActions) * 100) : 0;
    return rate;
  };

  if (isFinished) {
    return (
      <>
        <h1>Statistics</h1>
        <div id="statistics">
          <div className="statistics-container">
            <div className="statistic">{gameHistory.length}</div>
            <div className="label">Num Games</div>
          </div>
          <div className="statistics-container">
            <div className="statistic">{accurateRate()}</div>
            <div className="label">Overall Acc %</div>
          </div>
          <div className="statistics-container">
            <div className="statistic">
              {numCorrects}/{numActions}
            </div>
            <div className="label"> Num Corrects / Num Actions</div>
          </div>
        </div>
      </>
    );
  } else {
    return (
      <>
        <table className="pod-sum-table">
          <thead>
            <tr>
              <th>POT</th>
            </tr>
          </thead>
          <tbody className="pod-sum-tbody">
            <tr>
              <td>{sumPoints()} BB</td>
            </tr>
          </tbody>
        </table>
        <table className="accuracy-table">
          <thead>
            <tr>
              <th>Accuracy</th>
            </tr>
          </thead>
          <tbody className="accuracy-tbody">
            <tr>
              <td>
                {accurateRate()}% ({numCorrects}/{numActions})
              </td>
            </tr>
          </tbody>
        </table>
      </>
    );
  }
};

export default Accuracy;
