import React from "react";
import ReactModal from "react-modal";
import BackButton from "./BackButton";
import RangeButton from "./RangeButton";
import Accuracy from "./Accuracy";
import ShareButtons from "./ShareButtons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrashArrowUp } from "@fortawesome/free-solid-svg-icons";
import { faForwardStep } from "@fortawesome/free-solid-svg-icons";

import "./style/FinishModal.css";
import "./style/Batsu.css";

const FinishModal = ({
  showFinishModal,
  setShowFinishModal,
  gameState,
  setGameState,
  initialState,
  gameHistory,
  setGameHistory,
  setShowAnswerModal,
}) => {
  const style = {
    overlay: {
      backgroundColor: "rgba(0, 0, 0, 0.7)",
      backgroundImage: "linear-gradient(0deg, #000000, transparent)",
    },
    content: {
      background: "rgba(0, 0, 0, 0)",
      borderColor: "rgba(0, 0, 0, 0)",
      borderRadius: "10px",
    },
  };

  const nextGame = () => {
    setShowFinishModal(false);
    setGameState(initialState());
  };

  const resetGameHistory = () => {
    setGameHistory([]);
    alert("History reset completed");
  };

  return (
    <ReactModal isOpen={showFinishModal} style={style} ariaHideApp={false}>
      <div id="overlay">
        <div id="content">
          <div id="modal-accuracy">
            <Accuracy
              gameState={gameState}
              isFinished={true}
              gameHistory={gameHistory}
            />
          </div>
          <div className="game-buttons">
            <BackButton
              gameState={gameState}
              setGameState={setGameState}
              setShowFinishModal={setShowFinishModal}
              isFinished={true}
              gameHistory={gameHistory}
              setGameHistory={setGameHistory}
            />
            <button
              className="reset-game-button"
              onClick={() => resetGameHistory()}
            >
              <FontAwesomeIcon icon={faTrashArrowUp} />
            </button>
            <button className="next-game-button" onClick={() => nextGame()}>
              <FontAwesomeIcon icon={faForwardStep} />
            </button>
            <RangeButton
              gameState={gameState}
              setShowAnswerModal={setShowAnswerModal}
            />
          </div>
          <ShareButtons/>
        </div>
      </div>
    </ReactModal>
  );
};

export default FinishModal;
