import "../style/RangeTable.css";
import { raiseColor, callColor, foldColor, cardNumbers } from "../Utils";

const RangeTable = ({ range, focusCellIdx }) => {
  const cellStyle = (actionRate) => {
    return {
      backgroundImage: `linear-gradient(to right, ${raiseColor} ${
        actionRate[0]
      }%, ${callColor} ${actionRate[0]}% ${
        actionRate[0] + actionRate[1]
      }%, ${foldColor} ${actionRate[0] + actionRate[1]}% 100%`,
    };
  };

  const focusCellStyle = (i, j) => {
    if (!focusCellIdx) {
      return {};
    } else if (focusCellIdx[0] === i && focusCellIdx[1] === j) {
      return {
        border: "3px solid yellow",
      };
    } else {
      return {};
    }
  };

  return (
    <table className="range-table">
      <thead>
        <tr>
          <td></td>
          {[...Array(13)].map((_, i) => (
            <td key={i}>
              <div className="content table-header">{cardNumbers[i]}</div>
            </td>
          ))}
        </tr>
      </thead>
      <tbody>
        {[...Array(13)].map((_, i) => {
          return (
            <tr key={i}>
              <td>
                <div className="content table-header">{cardNumbers[i]}</div>
              </td>
              {[...Array(13)].map((_, j) => {
                return (
                  <td
                    className="range-cell"
                    key={i * 13 + j}
                    style={focusCellStyle(i, j)}
                  >
                    <div className="content" style={cellStyle(range[i][j])}>
                      {}
                    </div>
                  </td>
                );
              })}
            </tr>
          );
        })}
      </tbody>
    </table>
  );
};

export default RangeTable;
