import "./style/Game.css";
import React, { useEffect, useState } from "react";
import ActionButton from "./ActionButton";
import Player from "./Player";
import Answer from "./Answer";
import NextButton from "./NextButton";
import History from "./History";
import BackButton from "./BackButton";
import FinishModal from "./FinishModal";
import Accuracy from "./Accuracy";
import { useLocalStorage } from "./LocalStorage";
import RangeButton from "./RangeButton";

const numPlayers = 6;

// 0 ~ maxNum-1size
const random_array = (maxNum = 52, size = 12) => {
  let nums = [...Array(maxNum).keys()],
    ranNums = [];

  while (size--) {
    let pickUpElement = nums[Math.floor(Math.random() * nums.length)];
    ranNums.push(pickUpElement);
    nums = nums.filter((n) => n !== pickUpElement);
  }
  return ranNums;
};

const initialState = () => ({
  cardIndexes: random_array(),
  points: [...Array(numPlayers - 2).fill(0), 0.5, 1],
  actions: [...Array(numPlayers).fill(null)],
  activePoint: 1,
  currentPlayerIdx: 0,
  openRaiserIdx: null,
  numPlayers: 6,
  numRaise: 0,
  histories: [],
});

const Game = () => {
  const [gameState, setGameState] = useState(initialState());
  const [showFinishModal, setShowFinishModal] = useState(false);
  const [gameHistory, setGameHistory] = useLocalStorage("gameHistory", []);
  const [showAnswerModal, setShowAnswerModal] = useState(false);

  // 
  // 1: activePoint
  // 2: BB
  useEffect(() => {
    let finishFlag = true,
      everyoneActioned = true;
    for (const isActioned of gameState.actions) {
      if (!isActioned) everyoneActioned = false;
    }
    if (everyoneActioned) {
      // 1
      for (let i = 0; i < numPlayers; i++) {
        if (
          gameState.actions[i] !== "fold" &&
          gameState.points[i] !== gameState.activePoint
        )
          finishFlag = false;
      }
    } else {
      // 2
      for (let i = 0; i < numPlayers - 1; i++) {
        if (gameState.actions[i] !== "fold") finishFlag = false;
      }
    }
    if (finishFlag) {
      setGameHistory([...gameHistory, gameState.histories]);
      setShowFinishModal(true);
    }
  }, [gameState]);

  const isActionFinished = () => {
    const playerIdx = gameState.currentPlayerIdx;

    if (
      gameState.actions[playerIdx] && // BBBB
      (gameState.points[playerIdx] === gameState.activePoint ||
        gameState.actions[playerIdx] === "fold")
    )
      return true;
    else return false;
  };

  return (
    <div id="game">
      {isActionFinished() && (
        <FinishModal
          showFinishModal={showFinishModal}
          setShowFinishModal={setShowFinishModal}
          gameState={gameState}
          setGameState={setGameState}
          initialState={initialState}
          gameHistory={gameHistory}
          setGameHistory={setGameHistory}
          setShowAnswerModal={setShowAnswerModal}
        />
      )}
      <div id="game-container">
        <div id="hand">
          <Player gameState={gameState} />
        </div>
      </div>

      <div id="board-container">
        <div id="board">
          <div id="data-container">
            {isActionFinished() ? (
              <Answer
                gameState={gameState}
                showAnswerModal={showAnswerModal}
                setShowAnswerModal={setShowAnswerModal}
              />
            ) : (
              <div id="data">
                <History gameState={gameState} />
                <div id="game-accuracy">
                  <Accuracy
                    gameState={gameState}
                    isFinished={false}
                    gameHistory={gameHistory}
                  />
                </div>
              </div>
            )}
          </div>
          <div id="actions">
            <div id="action-button-container">
              {isActionFinished() ? (
                <>
                  <BackButton
                    gameState={gameState}
                    setGameState={setGameState}
                    setShowFinishModal={setShowFinishModal}
                    isFinished={false}
                  />
                  <RangeButton
                    gameState={gameState}
                    setShowAnswerModal={setShowAnswerModal}
                  />
                  <NextButton
                    gameState={gameState}
                    setGameState={setGameState}
                  />
                </>
              ) : (
                <ActionButton
                  gameState={gameState}
                  setGameState={setGameState}
                />
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Game;
