import React from "react";
import "./style/BackButton.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRotateLeft } from "@fortawesome/free-solid-svg-icons";

const BackButton = ({
  gameState,
  setGameState,
  setShowFinishModal,
  isFinished,
  gameHistory,
  setGameHistory,
}) => {
  const backAction = () => {
    const playerIdx = gameState.currentPlayerIdx;
    const history = gameState.histories.pop();

    if (gameState.actions[playerIdx] === "raise") {
      gameState.numRaise -= 1;
      gameState.activePoint /= 3;
      if (gameState.openRaiserIdx === playerIdx && gameState.numRaise === 0) {
        gameState.openRaiserIdx = null;
      }
    } else if (gameState.actions[playerIdx] === "allin") {
      gameState.activePoint = 27;
      gameState.numRaise -= 1;
    }
    gameState.actions[playerIdx] = history.previousAction;
    gameState.points[playerIdx] = history.previousPoint;
    setGameState({ ...gameState });

    if (isFinished) {
      gameHistory.pop();
      setGameHistory([...gameHistory]);
      setShowFinishModal(false);
    }
  };

  return (
    <button className="back-button" onClick={() => backAction()}>
      <FontAwesomeIcon className="icon-rotate-left" icon={faRotateLeft} />
    </button>
  );
};

export default BackButton;
