import Card from "./Card";
import "./style/Hand.css";
import { cards } from "./Utils";

const Hand = ({ firstIdx, secondIdx, visible }) => {
  const style = {
    display: visible ? "block" : "none",
  };

  return (
    <>
      <div className="card" style={style}>
        <Card card={cards[firstIdx]} />
      </div>
      <div className="card" style={style}>
        <Card card={cards[secondIdx]} />
      </div>
    </>
  );
};

export default Hand;
