import openRange from "./open";
import facingRaiseRange from "./facingRaise";
import { positions } from "../Utils";

// currentPlayerRaise
const numRaise = (gameState) => {
  if (gameState.actions[gameState.currentPlayerIdx] === "raise") {
    return gameState.numRaise - 1;
  } else {
    return gameState.numRaise;
  }
};

const hasSolution = (gameState) => {
  // 3bet
  if (numRaise(gameState) >= 2) {
    return false;
  }

  // 
  if (gameState.openRaiserIdx > gameState.currentPlayerIdx) {
    return false;
  }

  // currentPlayercaller
  for (let i = 0; i < gameState.numPlayers; i++) {
    if (i !== gameState.currentPlayerIdx) {
      if (gameState.actions[i] === "call") {
        return false;
      }
    }
  }

  return true;
};

export const rangeData = (gameState) => {
  if (!hasSolution(gameState)) return null;

  const playerIdx = gameState.currentPlayerIdx,
    openRaiserIdx = gameState.openRaiserIdx;

  if (numRaise(gameState) === 0) {
    return openRange[playerIdx];
  } else if (numRaise(gameState) === 1) {
    return facingRaiseRange[openRaiserIdx][playerIdx];
  }
};

export const rangeAction = (gameState) => {
  if (!hasSolution(gameState)) return null;

  const playerIdx = gameState.currentPlayerIdx,
    card1Idx = gameState.cardIndexes[playerIdx],
    card2Idx = gameState.cardIndexes[playerIdx + gameState.numPlayers],
    suited = Math.floor(card1Idx / 13) === Math.floor(card2Idx / 13);

  const range = rangeData(gameState);

  let firstNum = card1Idx % 13,
    secondNum = card2Idx % 13;

  if (firstNum !== 1 && firstNum < secondNum) {
    [firstNum, secondNum] = [secondNum, firstNum];
  }

  if (suited) {
    return range[12 - firstNum][12 - secondNum];
  } else {
    return range[12 - secondNum][12 - firstNum];
  }
};

export const rangeTableName = (playerIdx, openRaiserIdx) => {
  if (openRaiserIdx !== null && openRaiserIdx !== playerIdx) {
    return `${positions[playerIdx]} range when ${positions[openRaiserIdx]} opens`;
  } else {
    return positions[playerIdx];
  }
};
